import React, { Component } from 'react'
import {BrowserRouter as Router,Route} from 'react-router-dom';
import Header from './components/Header'
import Home from './container/Home';
import Api from './container/Api';
import About from './container/About';
import Start from './container/Start';
import Content from './Content';
import Login from './container/Login';
export default class App extends Component {
    render() {
        return (
            <Router>
                <div>
                    <Header/>
                </div>
                <div className='content'>
                    <div className="left">
                        <Route path='/home' component={Home}/>
                        <Route path='/start' component={Start}/>
                        <Route path='/api' component={Api}/>
                        <Route path='/about' component={About}/>
                        <Route path='/login' component={Login}/>
                        <Route path='/content/:id' component={Content}/>
                        <div style={{height:300,backgroundColor:"#fff",textAlign:"center",marginTop:20}}>评论</div>
                    </div>
                    <div className="right">
                        <div style={{height:100,textAlign:"center",backgroundColor:"#fff"}}>用户信息</div>
                        <div style={{height:150,textAlign:"center",marginTop:20,backgroundColor:"#fff"}}>广告</div>
                    </div>
                </div>
            </Router>
        )
    }
}