import React, { Component } from 'react'

export default class Login extends Component {
    handle(){
        var url = window.location.href.slice(0,-6);
        window.location.href=url+'/react/2';
    }
    render() {
        return (
            <div style={{backgroundColor:"#fff",borderBottom:'1px solid #f4f4f4',width:920,height:300}}>
                <div style={{backgroundColor:"#f3f3f3", width:920, height:25}}>
                    <span style={{color:"#88f996"}}>主页/</span>
                    <span style={{color:"#cacaca"}}>登录</span>
                </div>
                <div style={{width:500,height:276}}>
                    <div style={{marginTop:50,marginLeft:120}}>
                        用户名：<input style={{width:284,height:30,border:'1 solid #ccc',marginLeft:10,marginBottom:20}} type="text" />
                        <br/>
                        密码：<input style={{width:284,height:30,border:'1 solid #ccc',marginLeft:26,marginBottom:20}} type="password" />
                        <br/>
                        <button onClick={this.handle} style={{width:200,height:30,border:'1 solid #ccc',marginLeft:26,marginBottom:20}}>登录</button>
                    </div>
                </div>
            </div>
        )
    }
}
