import React, { Component } from 'react'
import {NavLink,Link,Route} from 'react-router-dom';
import Jinghua from '../components/Jinghua';
import All from '../components/All';
import Ask from '../components/Ask';
import Job from '../components/Job';
import Dev from '../components/Dev';
import Share from '../components/Share';

export default class Home extends Component {
    render() {
        return (
            <div>
                <div style={{backgroundColor:"#f4f4f4",borderBottom:'1px solid #f4f4f4',width:890,height:20,padding:15}}>
                    <Link to='/home/all/1'>全部</Link>
                    <Link to='/home/jinghua/1'>精华</Link>
                    <Link to='/home/share/1'>分享</Link>
                    <Link to='/home/ask/1'>问答</Link>
                    <Link to='/home/job/1'>招聘</Link>
                    <Link to='/home/dev/1'>客户端测试</Link>
                </div>
                <div>
                    <Route path='/home/all/:id' component={All}/>
                    <Route path='/home/jinghua/:id' component={Jinghua}/>
                    <Route path='/home/share/:id' component={Share}/>
                    <Route path='/home/ask/:id' component={Ask}/>
                    <Route path='/home/job/:id' component={Job}/>
                    <Route path='/home/dev/:id' component={Dev}/>
                </div>
                {/* <div>
                    
                </div> */}
            </div>
        )
    }
}