import React, { Component } from 'react';

export default class Content extends Component {
    constructor(){
        super();
        this.state = {
            data: []
        }
    }
    componentDidMount(){
        let text = this.props.match.params.id;
        fetch('https://cnodejs.org/api/v1/topic/'+text)
            .then((res)=>res.json())
            .then((res)=>{
                console.log(res.data)
                this.setState({
                    data: res.data
                });
            })
    }
    componentDidUpdate(prevProps,prevState){
        let text = this.props.match.params.id;
        fetch('https://cnodejs.org/api/v1/topic/'+text)
            .then((res)=>res.json())
            .then((res)=>{
                this.setState({
                    data: res.data
                });
            })
    }
    render(res){
        return (
            <div style={{backgroundColor:"#f4f4f4",width:900,height:'100%',padding:10}}>
                <div style={{backgroundColor:"#f4f4f4",width:900,height:40}}>
                    <span style={{width:25, height:20,textAlign:"center",fontSize:6,backgroundColor:"#80ff00",marginTop:5,marginLeft:5, float:"left"}}>置顶</span>
                    <span dangerouslySetInnerHTML={{__html:this.state.data.title}} style={{fontSize:20,fontWeight:"bold",marginLeft:5}}></span>
                </div>
                <div dangerouslySetInnerHTML={{__html:this.state.data.content}}></div>
            </div>
        )
    }
}
